'use client'
import React from 'react'
import Frontpage from '@/components/ui/frontpage'
import Community from '@/components/ui/community'
import CommunityCard from '@/components/ui/community-card'
import PaginationDemo from '@/components/ui/community-pagination'
import { useEffect , useState } from 'react'
import axios from 'axios'

const page = () => {
interface communities {
            id: number,
            communityOwnerName: string,
            communityName: string,
            communityAvatar: string,
            CommunityAvatarPublicId?: string,
            totalParticipants: number,
            communityOwnerId: number,
            createdAt?: string,
            updatedAt?:string 
}

  const [communities, setcommunities] = useState<communities[]>([])

  useEffect( () => {
    const fetchData = async()=>{
    try {
        const data = await axios.get(`http://localhost:3000/api/community/all-communities`)
        console.log(data.data?.community)
        setcommunities(data.data?.community)
        console.log('the data of the communities is : ' , communities)
    } catch (error) {
        console.log('the error at time of fetching the data is ', error)      
    }}

    fetchData()
  }, [])


  return (
    <div className='flex flex-col w-full h-full '>
    <Frontpage/>
    <Community/>
    <CommunityCard/>
    <PaginationDemo/>
    </div>
  )
}

export default page
